import React, { ReactNode } from 'react';
import classNames from 'classnames';

import type { Background } from '../types';
import { Heading, Text } from '../Typography';

import styles from './SubmissionMessage.module.scss';

export interface SubmissionMessageProps {
    title: string;
    description?: string;
    icon?: ReactNode;
    children?: ReactNode;
    background?: Background;
    warning?: boolean;
}

export const SubmissionMessage: React.FC<SubmissionMessageProps> = ({
    title,
    description,
    icon,
    children,
    background = 'main',
    warning,
    ...props
}) => {
    const iconWithClassname = React.cloneElement(icon as React.ReactElement, {
        className: classNames(styles.icon, {
            [styles.warning]: warning,
        }),
    });

    return (
        <div
            className={classNames(styles.base, {
                [styles['base--alternative']]: background === 'alternative',
            })}
            {...props}
        >
            {icon && iconWithClassname}
            <div className={styles.inner}>
                <div className={styles.content}>
                    <Heading
                        variant="h4"
                        className={classNames(styles.title, {
                            [styles.warning]: warning,
                        })}
                    >
                        {title}
                    </Heading>
                    {description && (
                        <Text className={styles.description}>
                            {description}
                        </Text>
                    )}
                </div>
                {children}
            </div>
        </div>
    );
};
